.introdution-container {
  width: 100%;
  height: 568px;
  width: 100%;
  height: 568px;
  background-image: url('../../assests/img/fundo.png');
  position: relative;
  margin-top: 70px;
  overflow-x: hidden;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
}

.stars-animation {
  animation: moveBackgrond 15s infinite ease-in-out alternate;
  position: absolute;
  background: linear-gradient(180deg, rgb(7 6 7), rgb(19 11 20) 70%, rgb(4 7 37));
  opacity: 0.5;
}

h1 {
  color: rgb(164 88 123);
  font-size: 2.5vw;
  text-shadow: 2px 2px 4px #58036e;
  width: 24%;
  height: 22vh;
  margin: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  text-align: center;
  align-content: center;
  place-items: center;
  z-index: 10;
}

.introdution-container > button {
  padding: 10px 22px;
  text-align: center;
  border-radius: 5px;
  border: solid 5px rgb(95 109 239);
  z-index: 9;
  font-family: sans-serif;
  font-size: large;
  font-weight: 600;
  color: white;
  background: #5f6def;
  margin: 0;
}

button > a {
  text-decoration: none;
  color: white;
}

@keyframes moveBackgrond {
    from {
      overflow-x: hidden;
      transform: translateX(-50px);
    }
}